:root {
  --primary: #0b4b3f;
  --primary-dark: #073832;
  --accent: #34d399;
  --accent-light: #ecfdf5;
  --sage: #64a6ac;
  --sage-light: #a7c4c7;
  --background: #f8faf9;
  --text-dark: #1a2f2c;
  --text-light: #4a5f5c;
  --white: #fff;
  --font-primary: "Plus Jakarta Sans", -apple-system, BlinkMacSystemFont, sans-serif;
  --font-secondary: "Frank Ruhl Libre", serif;
  --font-mono: "Inter", monospace;
  --spacing-xs: .25rem;
  --spacing-sm: .5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;
  --spacing-2xl: 3rem;
  --spacing-3xl: 4rem;
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 16px;
  --radius-xl: 24px;
  --radius-full: 9999px;
  --shadow-sm: 0 1px 2px #0000000d;
  --shadow-md: 0 4px 6px -1px #0000001a;
  --shadow-lg: 0 10px 15px -3px #0000001a;
  --shadow-xl: 0 20px 25px -5px #0000001a;
  --transition-fast: .15s ease;
  --transition-normal: .25s ease;
  --transition-slow: .35s ease;
  --z-negative: -1;
  --z-elevate: 1;
  --z-sticky: 100;
  --z-drawer: 200;
  --z-modal: 300;
  --z-popover: 400;
  --z-maximum: 999;
  --container-sm: 640px;
  --container-md: 768px;
  --container-lg: 1024px;
  --container-xl: 1280px;
  --container-2xl: 1400px;
}

