.navbar {
  width: 100%;
  z-index: var(--z-sticky);
  background: none;
  padding: 1.5rem 0;
  position: absolute;
  top: 0;
  left: 0;
}

.nav-container {
  max-width: var(--container-2xl);
  padding: 0 var(--spacing-xl);
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo img {
  filter: brightness(0) invert();
  height: 28px;
}

.nav-links {
  align-items: center;
  gap: var(--spacing-xl);
  margin: 0 auto;
  display: flex;
}

.nav-link {
  color: var(--white);
  transition: opacity var(--transition-fast);
  padding: .5rem var(--spacing-sm);
  background: none;
  font-size: .9375rem;
  font-weight: 500;
  text-decoration: none;
}

.nav-link:hover {
  opacity: .8;
}

.nav-right {
  align-items: center;
  gap: var(--spacing-lg);
  display: flex;
}

.dropdown {
  position: relative;
}

.dropdown-trigger {
  color: var(--white);
  cursor: pointer;
  padding: .5rem var(--spacing-sm);
  background: none;
  align-items: center;
  gap: .25rem;
  font-size: .9375rem;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.dropdown-trigger:after {
  content: "▾";
  transition: transform var(--transition-fast);
  margin-left: .25rem;
  font-size: 1.2em;
  line-height: 1;
}

.dropdown:hover .dropdown-trigger:after {
  transform: rotate(180deg);
}

.dropdown-content {
  background: var(--white);
  border-radius: var(--radius-lg);
  padding: var(--spacing-md);
  opacity: 0;
  visibility: hidden;
  min-width: 280px;
  transition: all var(--transition-fast);
  box-shadow: var(--shadow-lg);
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%)translateY(.5rem);
}

.dropdown:hover .dropdown-content {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%)translateY(0);
}

.dropdown-item {
  padding: var(--spacing-md);
  border-radius: var(--radius-md);
  color: var(--text-dark);
  transition: all var(--transition-fast);
  text-decoration: none;
  display: block;
}

.dropdown-item:hover {
  background: var(--accent-light);
  transform: translateX(4px);
}

.dropdown-item-title {
  margin-bottom: .25rem;
  font-size: .9375rem;
  font-weight: 600;
}

.dropdown-item-description {
  color: var(--text-light);
  font-size: .8125rem;
  line-height: 1.4;
}

.nav-button {
  color: var(--white);
  border-radius: var(--radius-full);
  transition: all var(--transition-fast);
  background: #ffffff1a;
  border: 1px solid #fff3;
  padding: .625rem 1.25rem;
  font-size: .9375rem;
  font-weight: 600;
}

.nav-button:hover {
  background: #fff3;
  transform: translateY(-1px);
}

.navbar.scrolled {
  backdrop-filter: blur(8px);
  box-shadow: var(--shadow-md);
  background: #ffffffe6;
}

.navbar.scrolled .nav-link {
  color: var(--text-dark);
}

.navbar.scrolled .logo img {
  filter: none;
}

.navbar.scrolled .nav-button {
  background: var(--primary);
  color: var(--white);
  border: none;
}

.hero {
  background: linear-gradient(135deg, var(--primary-dark) 0%, var(--primary) 30%, var(--accent) 70%, #6ee7b7 100%);
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}

.hero:after {
  content: "";
  background: var(--white);
  clip-path: polygon(0 100%, 100% 100%, 100% 0, 75% 50%, 50% 0, 25% 50%, 0 0);
  width: 100%;
  height: 30%;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.hero-container {
  z-index: var(--z-elevate);
  max-width: var(--container-2xl);
  padding: var(--spacing-3xl) var(--spacing-xl);
  margin: 0 auto;
  position: relative;
}

.hero-content {
  gap: var(--spacing-2xl);
  max-width: var(--container-2xl);
  padding: var(--spacing-3xl) var(--spacing-xl);
  grid-template-columns: 1fr 1fr;
  align-items: center;
  margin: 0 auto;
  display: grid;
}

.hero-left {
  max-width: 600px;
}

.hero-title {
  margin-bottom: var(--spacing-xl);
  color: var(--white);
  font-size: 4.8rem;
  font-weight: 800;
  line-height: 1.15;
  font-family: var(--font-primary);
  letter-spacing: -.03em;
}

.gradient-text {
  background: linear-gradient(135deg, #6ee7b7 0%, var(--accent) 50%, #10b981 100%);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  display: inline-block;
}

.hero-subtitle {
  color: #ffffffe6;
  margin-bottom: var(--spacing-2xl);
  max-width: 800px;
  font-size: 1.5rem;
  font-family: var(--font-primary);
  letter-spacing: .01em;
  font-weight: 400;
  line-height: 1.6;
}

@media (width <= 1024px) {
  .hero-title {
    font-size: 4rem;
  }

  .hero-subtitle {
    font-size: 1.4rem;
  }
}

@media (width <= 768px) {
  .hero-content {
    gap: var(--spacing-xl);
    text-align: center;
    grid-template-columns: 1fr;
  }

  .hero-left {
    margin: 0 auto;
  }

  .hero-title {
    font-size: 3rem;
  }

  .hero-subtitle {
    margin-bottom: var(--spacing-xl);
    font-size: 1.25rem;
  }
}

@media (width <= 480px) {
  .hero-title {
    font-size: 2.5rem;
  }

  .hero-subtitle {
    font-size: 1.1rem;
  }
}

.mock-ui-group {
